    >
      <div id="listbackground">
        <div id="title"> </div>
        <b-row class="mb-3" id="menu">
          <b-col id="subtitle"></b-col>
          <div class="w-100"></div>
          <b-col id="categorytext">{{ this.moimData.title }}</b-col>
        </b-row>
        <b-row class="mb-3" id="menu">
          <b-col id="subtitle"></b-col>
          <div class="w-100"></div>
          <b-col id="categorytext" v-if="moimData.length!=0"
            >{{ this.moimData.createDate.split(/-|T/)[0] }} 
            {{ this.moimData.createDate.split(/-|T/)[1] }}
            {{ this.moimData.createDate.split(/-|T/)[2] }}</b-col
          >
        </b-row>
        <b-row class="mb-3" id="menu">
          <b-col id="subtitle"></b-col>
          <div class="w-100"></div>
          <b-col id="categorytext">{{
            this.moimData.sido +
            " " +
            this.moimData.sigungu +
            " " +
            this.moimData.dong
          }}</b-col>
        </b-row>
        <b-row>
          <b-col>
            <b-row class="mb-3" id="menu">
              <b-col id="subtitle"></b-col>
              <div class="w-100"></div>
              <b-col id="categorytext" v-if="this.moimData.isFreeEnter === 'Y'"
                > </b-col
              >
              <b-col id="categorytext" v-else> </b-col>
            </b-row>
          </b-col>
          <b-col>
            <b-row class="mb-3" id="menu">
              <b-col id="subtitle"></b-col>
              <div class="w-100"></div>
              <b-col id="categorytext" v-if="this.moimData.isPublic === 'Y'"
                ></b-col
              >
              <b-col id="categorytext" v-else></b-col>
            </b-row>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <b-row class="mb-3" id="menu">
              <b-col id="subtitle"></b-col>
              <div class="w-100"></div>
              <b-col id="categorytext">{{ this.moimData.maxMember }}</b-col>
            </b-row>
          </b-col>
          <b-col>
            <b-row class="mb-3" id="menu">
              <b-col id="subtitle"> </b-col>
              <div class="w-100"></div>
              <b-col id="categorytext">3</b-col>
            </b-row>
          </b-col>
        </b-row>
      </div>
      <div id="listbackground">
        <div id="title"></div>
        <div v-if="this.moimData.content !== ''">
          {{ this.moimData.content }}
        </div>
        <div v-else>   .</div>
      </div>
    </div>
  </div>
</template>

<script>
import { MoimDetail } from "@/services/moim";

export default {
  data() {
    return {
      moimData: [],
    };
  },
  methods: {
    async setData(id) {
      let moimData = await MoimDetail(id);
      this.moimData = moimData.data;
    },
  },
  created() {
    //id api        .
    var id = this.$store.getters["searchStore/getSelectedMoimId"];
    this.setData(id);
  },
};
</script>
